<template>

<div class="bookpage">
    <BookItem :slug="slug" :titleprop="book.title" :authorprop="book.author" :themeprop="book.themes" :numberprop="book.number" v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }"></BookItem>
    <BookSummaryItem v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }">{{book.booksummary}}</BookSummaryItem>
    <BookDownloadItem v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }">Kindle</BookDownloadItem>
</div>

</template>


<script>
import store from '@/store'

import BookItem from '@/components/BookItem.vue';
import BookSummaryItem from '@/components/bookpagecomponents/BookSummaryItem.vue';
import BookDownloadItem from '@/components/bookpagecomponents/BookDownloadItem.vue';



export default {
    name: "BookPage",
    components: {
        BookItem,
        BookSummaryItem,
        BookDownloadItem
        // BookCatalog
    },
    data(){
        return{
        }
    },
    props:{
        slug: {
            type: String,
            required: true
        }
    },
    computed: {
        book(){
            return store.books.find(
                book => book.slug === this.slug
            )
        }
    } 
}
</script>


